
@font-face {
  font-family: Avenir-Book;
  src: url(./fonts/Avenir-Book.ttf);
}

@font-face {
  font-family: Avenir-Heavy;
  src: url(./fonts/Avenir-Heavy.ttf);
}

* {
  padding: 0;
  box-sizing: border-box;
}

.big-search-form {
  width: 100%;
  height: 70px;
}

.search-form {
  display: flex;
  justify-content: center;
  margin: 20px auto 0;
}

.search-form__label {
  position: relative;
  display: block;
  width: 100%;
}

.search-form__input_big {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.search-form__input {
  width: 100%;
  height: 100%;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  color: #3d4e61;
  font-weight: 300;
  font-family: Avenir-Book, sans-serif;
  box-shadow: 0 1px 8px rgb(61 78 97 / 10%);
  outline: none;
}

.search-form__input:hover {
  box-shadow: 0 3px 8px rgb(61 78 97 / 20%);
}

.search-form__input:focus {
  background: linear-gradient(
    180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}

.search-form__label:after {
  position: absolute;
  content: "";
  display: block;
}

.search-form__label_big:after {
  background-image: url(images/Search.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.small-search-form {
  width: 100%;
  height: 42px;
}

.search-form__input_small {
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.search-form__label_small:after {
  background-image: url(images/Search.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}
